<template>
  <div class="main">
    <el-carousel height="20rem" motion-blur>
      <el-carousel-item>
        <img src="../../../public/image/lun001.png" alt="" />
      </el-carousel-item>
      <el-carousel-item>
        <img src="../../../public/image/lun002.png" alt="" />
      </el-carousel-item>
      <el-carousel-item>
        <img src="../../../public/image/lun003.png" alt="" />
      </el-carousel-item>
      <el-carousel-item>
        <img src="../../../public/image/lun004.png" alt="" />
      </el-carousel-item>
    </el-carousel>
    <div class="echartsOne" id="echarts-one"></div>
    <!-- <div class="echartsOne" id="echarts-two"></div> -->
    <div class="datePicker">
      <el-calendar v-model="Datevalue" />
    </div>
    <div class="list">
      <div class="listtop">
        <p>任务列表</p>
      </div>
      <el-table
        :data="tableData"
        :default-sort="{ prop: 'date', order: 'descending' }"
        border
        style="width: 100%"
        height="250"
      >
        <el-table-column prop="date" label="日期" sortable width="180" />
        <el-table-column prop="name" label="负责人" width="180" />
        <el-table-column prop="name" label="项目进展" width="180" />
        <el-table-column prop="address" label="Address" />
        <el-table-column prop="name" label="操作人" width="180" />
        <el-table-column fixed="right" label="操作" width="180">
          <template #default="scope">
            <el-button
              link
              type="primary"
              size="small"
              @click="changinfo(scope.$index, scope.row)"
              v-if="scope.row.subinfo"
            >
              修改信息
            </el-button>
            <el-button
              link
              type="primary"
              size="small"
              @click="deleteinfo(scope.$index, scope.row)"
              v-else
            >
              填写信息
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <listAss :show="show" @close="handleClose" />
    </div>
  </div>
</template>


<script>
import * as echarts from "echarts";
import listAss from "../../components/listAss.vue";
export default {
  components: {
    listAss,
  },
  data() {
    return {
      show: 0,
      chartOne: null,
      chartTwo: null,
      gaugeData: [
        {
          value: 20,
          name: "目标意向数20",
          title: {
            fontSize: "1.5rem", // 设置标题字体大小
            offsetCenter: ["0%", "-32%"],
          },
          detail: {
            valueAnimation: true,
            offsetCenter: ["0%", "-20%"],
          },
        },
        {
          value: 40,
          name: "目标推销数40",
          title: {
            fontSize: "1.5rem", // 设置标题字体大小
            offsetCenter: ["0%", "-2%"],
          },
          detail: {
            valueAnimation: true,
            offsetCenter: ["0%", "10%"],
          },
        },
        {
          value: 60,
          name: "目标成交数60",
          title: {
            fontSize: "1.5rem", // 设置标题字体大小
            offsetCenter: ["0%", "28%"],
          },
          detail: {
            valueAnimation: true,
            offsetCenter: ["0%", "40%"],
          },
        },
      ], // 定义仪表图数据数组，每个对象包含值、名称、标题和详细信息
      tableData: [
        {
          date: "2000-05-03",
          name: "Tom",
          address: "No. 189, Grove St, Los Angeles",
          type: 1,
          subinfo: {
            phone: "18976543210",
            email: "tom@qq.com",
            address: "No. 189, Grove St, Los Angeles",
            date: "2016-05-03",
            name: "Tom",
          },
        },
        {
          date: "2000-05-02",
          name: "Tom",
          address: "No. 189, Grove St, Los Angeles",
          subinfo: {
            phone: "21312314",
            email: "to2323413413413m@qq.com",
            address: "23412341324Los Angeles",
            date: "2012-05-03",
            name: "BBBBB",
          },
        },
        {
          date: "2016-05-04",
          name: "Tom",
          address: "No. 189, Grove St, Los Angeles",
        },
        {
          date: "2016-05-01",
          name: "Tom",
          address: "No. 189, Grove St, Los Angeles",
        },
        {
          date: "2016-05-01",
          name: "Tom",
          address: "No. 189, Grove St, Los Angeles",
        },
        {
          date: "2016-05-01",
          name: "Tom",
          address: "No. 189, Grove St, Los Angeles",
        },
        {
          date: "2016-05-01",
          name: "Tom",
          address: "No. 189, Grove St, Los Angeles",
        },
        {
          date: "2016-05-01",
          name: "Tom",
          address: "No. 189, Grove St, Los Angeles",
        },
        {
          date: "2016-05-01",
          name: "Tom",
          address: "No. 189, Grove St, Los Angeles",
        },
        {
          date: "2016-05-01",
          name: "Tom",
          address: "No. 189, Grove St, Los Angeles",
        },
      ],
      Datevalue: new Date(),
    };
  },
  methods: {
    echarts() {
      // 基于准备好的dom，初始化echarts实例
      var myChart = echarts.init(document.getElementById("echarts-one"));
      console.log(this.gaugeData);

      // 定义仪表图的配置选项
      var option = {
        title: {
          text: "目标完成度快速查看",
          subtext: "Quick View of Goal Completion",
          fontSize: "1rem",
        },
        series: [
          {
            type: "gauge",
            startAngle: 90,
            endAngle: -270,
            pointer: {
              show: false,
            },
            progress: {
              show: true,
              overlap: false,
              roundCap: true,
              clip: false,
              itemStyle: {
                borderWidth: 1,
                borderColor: "#464646",
              },
            },
            axisLine: {
              lineStyle: {
                width: 40,
              },
            },
            splitLine: {
              show: false,
              distance: 0,
              length: 10,
            },
            axisTick: {
              show: false,
            },
            axisLabel: {
              show: false,
              distance: 50,
            },
            data: this.gaugeData,
            title: {
              fontSize: "1rem",
            },
            detail: {
              width: "50rem",
              height: "12rem",
              fontSize: "1.5rem",
              color: "inherit",
              borderColor: "inherit",
              borderRadius: 20,
              borderWidth: 1,
              formatter: "{value}%",
            },
          },
        ],
      };

      // 每2秒更新一次仪表图数据，并重新设置图表选项
      setInterval(() => {
        this.gaugeData[0].value = +(Math.random() * 100).toFixed(2);
        this.gaugeData[1].value = +(Math.random() * 100).toFixed(2);
        this.gaugeData[2].value = +(Math.random() * 100).toFixed(2);
        myChart.setOption({
          series: [
            {
              data: this.gaugeData,
              pointer: {
                show: false,
              },
            },
          ],
        });
      }, 2000);
      option && myChart.setOption(option);

      window.onresize = function () {
        myChart.resize();
      };
    },
    // 表格操作
    changinfo(index, row) {
      console.log("修改信息", index);
      console.log("修改信息", row);
      console.log("修改信息", row.subinfo);
      console.log("修改信息", row.subinfo.address);
      this.show = 1;
    },
    handleClose(data) {
      console.log("关闭弹窗", data);
      this.show = 0;
    },
    deleteinfo(index, row) {
      console.log(" 填写信息", index);
      this.show = 1;
    },
  },
  mounted() {
    this.echarts();
    // this.echarts2();
  },
};
</script>

<style scoped>
.main {
  width: 100%;
  height: calc(100vh - 5rem);
  background-color: #f5f5f5;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start;
  align-content: flex-start;
  justify-content: space-between;
  overflow: auto;
}
/* 轮播图设置 */
.el-carousel {
  width: 100%;
  height: 20rem;
  /* background-color: red; */
}
.el-carousel__item {
  width: 100%;
  /* background-color: red; */
}
.el-carousel__item img {
  width: 100%;
  height: auto;
}

/* echarts设置 */
.echartsOne {
  width: 42%;
  height: 50rem;
  margin: 2rem 5rem;
  background-color: #fff;
  padding: 2rem;
}

/* 日历 */
.datePicker {
  width: 42%;
  /* height: 50rem; */
  margin: 2rem 5rem;
  background-color: #fff;
  padding: 2rem;
}

/* 列表 */
.list {
  margin: 2rem 5rem;
  /* height: 30rem; */
  width: 100%;
  padding: 2rem;
  background-color: #fff;
  /* overflow: auto; */
}

.listtop {
  display: flex;
  margin-bottom: 2rem;
}
.listtop p {
  font-size: 3rem;
}
</style>